.about {


  &__bg {
    position: relative;
    display: flex;
    align-items: center;
    background: url('https://mir-s3-cdn-cf.behance.net/project_modules/fs/1e486659756233.5a2e90161b0be.jpg') no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    min-height: 525px;
    z-index: 1;

    &::before {
      content: '';
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }

    .title {
      color: #fff;
      font-weight: bold;
    }


  }

  &__item {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background: linear-gradient(0deg, rgba(224,34,73,1) 0%, rgba(181,35,65,1) 100%);
    border-radius: 15px;
    width: 150px;
    height: 150px;
    margin-bottom: 2rem;

    svg {
      width: 70px;
      height: 70px;
      color: #fff;
    }

  }

  &__item-container {
    position: relative;
    z-index: 1;
    margin-top: -4.6rem;
    padding-bottom: 2rem;

  }

  &__item-wrapper {
    position: relative;
    &::before {
      content: '';
      background: url('../img/svg/image 3.svg') no-repeat;
      background-size: contain;
      display: block;
      position: absolute;
      left: 0;
      top: 100px;
      width: 380px;
      height: 650px;
    }

    &::after {
      content: '';
      background: url('../img/svg/image 4.svg') no-repeat;
      background-size: contain;
      display: block;
      position: absolute;
      right: 0;
      top: 500px;
      width: 585px;
      height: 755px;
    }
  }

  &__item-title {
    font-weight: 700;
  }

  &__content-row {
    max-width: 945px;
    width: 100%;
    margin: 0 auto;
    padding: 2rem 0;

    .title {
      color: #DA2F4A;
      padding-bottom: 2rem;
    }
  }
}
