.info-block {
  margin-top: -57px;
  z-index: 1;
  position: relative;

  &__wrap {
    overflow: hidden;
    position: relative;
    z-index: 1;
    background: linear-gradient(234.99deg, #EA6F20 30.97%, #DA2E4B 67.21%);
    border-radius: 20px;
    padding: 1.5rem;


    &::before {
      content: '';
      background: url("../img/svg/logo-2.svg") no-repeat;
      background-size: auto;
      background-size: contain;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      background-position: right;
      z-index: -1;
    }
  }

  &__wrap-content {
    display: flex;
    align-items: center;
  }

  .brand-logo {
    background-color: #fff;
    border-radius: 5px;
    padding: 1rem 1.5rem;

    img {
      width: 100%;
      max-width: 188px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    margin-left: 2.5rem;

    a {
      text-decoration: none;
      color: #fff;
    }

    svg {
      margin-right: 8px;
      fill: #fff;
    }
  }
}



.content-container {
  max-width: 945px;
  width: 100%;
}

.content {
  &__row {
    max-width: 945px;
    width: 100%;
    margin: 0 auto;
    padding: 2rem 0;

    .title {
      background: linear-gradient(181.62deg, rgba(234, 111, 32, 0.8) -17.53%, rgba(218, 46, 75, 0.8) 98.62%);
      background-clip: border-box;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      display: table;
      padding-bottom: 2rem;
    }
  }
}


.coworking {


  &__bg {
    position: relative;
    display: flex;
    align-items: center;
    background: url('../img/coworking/01.jpg') no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    min-height: 525px;
    z-index: 1;

    &::before {
      content: '';
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }

    .title {
      color: #fff;
      font-weight: bold;
    }


  }
}

.table> :not(caption)>*>* {
  white-space: nowrap;
}


.article-in {
  margin: 1rem 0;

  &__image {
    border-radius: 5px;
    overflow: hidden;
    height: 300px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .title {
    margin-bottom: 1.5rem;
    background: linear-gradient(181.62deg, rgba(234, 111, 32, 0.8) -17.53%, rgba(218, 46, 75, 0.8) 98.62%);
    background-clip: border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: table;
  }

  .text {
    color: #000000;
    margin-bottom: 2.5rem;
  }

  &__date {
    padding: 1rem 0;
    color: rgba(0, 0, 0, 0.55);
  }

  &__sidebar {
    position: relative;
    margin-left: 1rem;
    padding: 0 3rem;
    height: 100%;

    &::before {
      content: '';
      background: url('../img/svg/figure.svg') no-repeat;
      width: 100%;
      height: 100%;
      background-size: contain;
      display: block;
      position: absolute;
      right: 0;
      background-position: right;
      z-index: -1;
    }
  }

  &__sidebar-title {
    margin-bottom: 1.2rem;
  }

  &__item-images {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    height: 200px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-images {
    &::before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      opacity: 0;
      transition: all 500ms ease;
    }

  }


  &__item {
    margin-bottom: 2.5rem;
    &:hover .article-in__item-images::before {
        opacity: 1;
    }

    &:hover .article-in__item-title {
        color: rgba(0, 0, 0, 0.7);
    }

    a {
        text-decoration: none;
    }


  }

  &__item-title {
    font-weight: normal;
    color: #0F0F0F;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  }

  &__item-date {
    padding: 1rem 0 0.5rem;
    color: rgba(0, 0, 0, 0.55);
  }

}
