.store {
  &__section {
    position: relative;
    display: flex;
    align-items: center;
    background: linear-gradient(234.99deg, #EA6F20 30.97%, #DA2E4B 67.21%);
    min-height: 330px;
    z-index: 1;
    &::before {
        content: '';
        background: url('../img/svg/logo-2.svg') no-repeat;
        background-size: contain;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        background-position: right;
        z-index: -1;
    }

    .title {
      color: #fff;
      font-weight: bold;
      letter-spacing: 4px;
    }



  }

  &__catalog {
      margin-top: -57px;
  }


  &__input {
    display: flex;
    justify-content: center;
    margin: 0 2rem;

    .form-control {
      background-color: transparent;
      border-left: 0;
      border-right: 0;
      border-top: 0;
      border-radius: 0;
      color: #fff;
      padding-left: 0;
      max-width: 220px;
      border-color: #fff;

      &:focus {
        box-shadow: none;
      }

      &::placeholder {
        color: #fff;
      }
    }
  }

  &__card {
    z-index: 2;
    padding: 1rem;
    border-radius: 20px;
    border: 0;
    box-shadow: 0 6px 15px rgba(0, 0, 0, .147);
    transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

    &:hover {
      transform: translateY(-6px);
    }

    .card-footer {
      border: 0;
      background-color: transparent;
      text-align: center;
      color: #898989;
    }

    .card-title {
      font-size: 18px;
      font-weight: 700;
      margin-bottom: 1rem;
    }
  }

  &__card-brand {
    height: 135px;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      max-width: 200px;
      width: 100%;
    }
  }

  &__card-time {
    display: flex;
    align-items: center;
    color: #8A8A8A;
    letter-spacing: 1px;
    margin-bottom: 5px;

    svg {
      margin-right: 13px;
    }
  }

  &__card-link {
    display: flex;
    align-items: center;

    a {
      color: #8A8A8A;
      text-decoration: none;
      transition: color 0.15s ease-in-out;

      &:hover {
        color: rgba(0, 0, 0, 0.7);
      }
    }

    svg {
      margin-right: 13px;
    }
  }



  &__load-more-container {
      margin-top: 5rem;
  }

  &__load-more {
    display: flex;
    align-items: center;
    border: 0;
    background-color: transparent;
    transition: all 0.3s;
    color: #000000;
  }

  &__load-more-icons {
    padding: 10px;
    background: #F5F5F5;
    border-radius: 15px;
    margin-right: 18px;

    &::before {
      content: '';
      background: url('../img/svg/plus.svg') no-repeat;
      background-size: contain;
      width: 25px;
      height: 25px;
      display: block;
    }
  }


  &__load-more:active {
	-webkit-transform: translateY(2px);
	-ms-transform: translateY(2px);
	transform: translateY(2px);
}




}
