.slider-main {
  &__image {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
  }

  &__content {
    min-height: calc(100vh - 209px);
    display: flex;
    align-items: start;
    z-index: 1;
    position: relative;
    flex-direction: column;
    justify-content: flex-end;
    color: #fff;
  }

  &__content-inner {
    max-width: 500px;
    width: 100%;
    padding-bottom: 25px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__more {
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    transition: color 0.15s ease-in-out;

    &:hover {
      color: #fff;
    }

    &:hover svg {
      transform: translateX(8px);
    }

    svg {
      transition: .3s ease;
      margin-left: 20px;
    }
  }

  .next1 {
    position: absolute;
    bottom: 25px;
    right: 30px;
    z-index: 1;
    background: rgba(245, 245, 245, 0.19);
    border-radius: 20px;
    padding: 20px;
  }

  .prev1 {
    position: absolute;
    right: 100px;
    bottom: 25px;
    z-index: 1;
    border-radius: 20px;
    padding: 20px;
    background: rgba(245, 245, 245, 0.19);

  }

  .prev1.swiper-button-disabled {
    opacity: 0.2;
    background: transparent;
  }

  .next1.swiper-button-disabled {
    opacity: 0.2;
    background: transparent;
  }



}


.slider-event {
  margin: 120px 0 0;

  .title {
    padding-bottom: 60px;
  }

  .card {
    overflow: hidden;
    border-radius: 20px;
    border: 0;
  }

  .card-img-wrap {
    position: relative;
    height: 350px;

    &:hover.card-img-wrap::before {
      opacity: 1;
    }

    &::before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      opacity: 0;
      transition: all 500ms ease;
    }



  }

  .card-img-top {
    object-fit: cover;
    height: 100%;
  }


  .card-title a {
    text-decoration: none;
    color: #1F1F1F;
    font-weight: 700;
    transition: color 0.15s ease-in-out;

    &:hover {
      color: #DA2F4A;
    }
  }

  .card-body {
    background: #F4F4F4;
    padding: 0.8rem 1rem;
  }

  .card-text {
    color: #EC6329;
    font-size: 12px;
    font-weight: normal;
    margin-bottom: 1.5rem;
  }



}

.wave {
  background: url('../img/svg/wave.svg') repeat-x;
  position: absolute;
  bottom: 0;
  background-size: contain;
  width: 100%;
  height: 100%;
  background-position: bottom;
}


.slider-controls {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: space-between;
  height: 60px;
  margin-top: 45px;

  &__all {
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
    text-decoration: none;
    display: flex;
    align-items: center;

    svg {
      transition: .3s ease;
      margin-left: 20px;
    }

    &:hover {
      color: #DA2F4A;
    }


    &:hover svg {
      transform: translateX(8px);
    }
  }

  &__btn {
    display: flex;
  }

  .slider-next {
    background: #F5F5F5;
    border-radius: 20px;
    padding: 20px;

    svg {
      fill: #5B5B5B;
    }
  }

  .slider-prev {
    border-radius: 20px;
    padding: 20px;
    background: #F5F5F5;
    margin-right: 0.5rem;

    svg {
      fill: #5B5B5B;
    }

  }

  .slider-prev.swiper-button-disabled {
    opacity: 0.2;
    background: transparent;
  }

  .slider-next.swiper-button-disabled {
    opacity: 0.2;
    background: transparent;
  }


}

.slider-brand {
  margin: 110px 0 50px;

  .title {
    margin-bottom: 40px;
  }

  &__item {
    border: 1px solid #DADADA;
    border-radius: 20px;
    height: 245px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;

    img {
      width: 100%;
      max-width: 250px;
    }
  }
}

.slider-card {

  &__item {
    position: relative;
    height: 270px;
    border-radius: 20px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    a::before {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
      opacity: 0;
      transition: all 500ms ease;
    }

    &:hover a::before {
      opacity: 1;
    }
  }
}
