@media (min-width: 350px) and (max-width: 575px) {

  .swiper-events .swiper-slide {
    width: 300px !important;
  }

  .swiper-brand .swiper-slide {
    width: 245px !important;
  }

  .swiper-card .swiper-slide {
    width: 270px !important;
  }

}

@media (min-width: 350px) and (max-width: 768px) {

  .swiper-news .swiper-slide {
    width: 300px !important;
  }

}

@media (max-width: 350px) {

  .header .navbar-top__mob-logo img {
    max-width: 140px;
  }

  .swiper-news .swiper-slide {
    width: 250px !important;
  }

  .slider-event .slider-controls {
    margin-top: 10px;
  }

  .about__item-container .about__item .about__item-icons {
    width: 80px;
    height: 80px;
  }

  .about__item-container .about__item .about__item-icons svg {
    width: 30px;
    height: 30px;
  }

}

@media (max-width: 575px) {


  .store__catalog .store__load-more-container {
    margin-top: 3rem;
  } 


  .card .store__card-brand {
    height: 100px;
  }

  .card .store__card-brand img {
    max-width: 150px;
  }

  .swiper-card .slider-card__item {
    height: 180px
  }


  section .swiper-brand .slider-brand__item {
    height: 120px;
  }

  body section.slider-event .card-img-wrap {
    height: 180px;
  }

  section.slider-main .swiper-slide .slider-main__content {
    min-height: 300px
  }

  .features__inner .features__btn {
    padding-top: 25px;
  }

  .swiper-slide .slider-brand__item img {
    max-width: 150px;
  }


  .swiper-slide .slider-brand__item {
    padding: 0;
  }

  section .slider-controls {
    display: none;
  }

  .features__inner .features__item svg {
    width: 60px;
    height: 60px;
  }

  .features__inner .features__item {
    padding: 0;
  }

  .article-in aside.article-in__sidebar {
    padding: 0;
  }



  .about__section .about__item-wrapper::before {
    content: none;
  }

  .about__section .about__item-wrapper::after {
    content: none;
  }


  section.store__section::before {
    content: none;
  }

  .slider-main .next1,
  .slider-main .prev1 {
    display: none;
  }

  .slider-main__content .slider-main__title {
    margin-bottom: 10px;
  }

  .slider-main__content .slider-main__content-inner {
    padding-bottom: 15px;
  }

  .slider-main .slider-main__image .wave {
    height: 143px;
  }

}

@media (min-width: 768px) {

  .swiper-news .swiper-wrapper {
    flex-direction: column;
  }



}

@media (max-width: 767px) {

  section.slider-event .card-text {
    margin-bottom: 0.5rem;
  }


  section.slider-event .card-img-wrap {
    height: 280px;
  }

  .swiper-brand .slider-brand__item {
    height: 180px;
  }

  aside.article-in__sidebar::before {
    content: none;
  }

  section.store__catalog {
    margin-top: -50px;
  }

  .coworking__section .coworking__bg {
    min-height: 300px;
  }

  .info-block__wrap-content {
    flex-direction: column;
  }

  .info-block__wrap-content .info-block__contacts {
    margin-left: 0;
    padding-top: 2rem;
  }

  .about__section .about__bg {
    min-height: 300px;
  }

  .info-block .info-block__wrap::before {
    content: none;
  }


  .contact__form-wrap .contact__form::before {
    content: none;
  }

  .map__section .map {
    height: 280px
  }

  .contact__section .contact__form-wrap {
    padding-bottom: 0;
  }

  .store__section {
    text-align: center;
  }

  .store__input .form-control {
    margin-bottom: 0.5rem;
  }

  .store__input {
    align-items: center;
  }

  .store__input {
    flex-direction: column;
  }

  .features .features__inner::before {
    content: none;
  }

  .features .features__inner {
    padding: 45px 0
  }

  section.slider-brand {
    margin: 50px 0;
  }

  section.slider-event {
    margin: 50px 0 0;
  }

  section.slider-event .title {
    padding-bottom: 30px;
  }

  .slider-main .swiper-slide .slider-main__content {
    min-height: 400px;
  }

  .footer__logo {
    display: flex;
    justify-content: center;
  }

  .footer .footer__menu {
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
  }

  .footer__menu li {
    margin-right: 1rem;
  }

  .footer__menu li {
    padding-top: 15px;
  }

  footer.footer {
    padding: 20px 0;
  }

  .footer__copyright {
    justify-content: center;
  }

  .footer__info {
    flex-direction: column;
    align-items: center;
  }

  .features__btn {
    flex-direction: column;
    align-items: center;
  }

  .features__btn .btn__light {
    margin-bottom: 20px;
  }
}


@media (max-width: 991px) {

  .slider-main__image .wave {
    background: none;
  }

  .slider-main__content-inner {
    background: linear-gradient(0deg, rgba(231,74,50,1) 0%, rgba(237,105,26,1) 100%);
    padding: 0 12px;
  }

  aside.article-in__sidebar {
    padding: 0 1rem;
  }

  .article-in__main .article-in__image {
    height: 248px;
  }

  .about__item .about__item-icons {
    width: 100px;
    height: 100px;
  }

  .about__item .about__item-icons svg {
    width: 50px;
    height: 50px;
  }

  .contact__form-wrap .contact__form {
    padding: 3.2rem 0.75rem;
  }

  .header {
    position: sticky;
    top: 0;
    z-index: 1020;
    background-color: #fff;
    box-shadow: 0 5px 5px rgba(0, 0, 0, .050153);
  }

  header.header .navbar-top {
    border-bottom: 0;
  }

  .navbar-bottom {
    display: none;
  }

  .footer .footer__info {
    margin: 0;
  }

  .footer__copyright {
    padding-bottom: 10px;
  }

  .navbar-top__mob-logo {
    display: flex;
    width: 100%;
    justify-content: center;
  }

  .navbar-top__mob-logo img {
    max-width: 175px;
  }

  body .header .navbar-top {
    padding: 0;
  }

  .mob__social a {
    display: flex;
  }



  .slider-main__content .slider-main__content-inner {
    max-width: 100%;
  }

}

@media (min-width: 991px) {
  .navbar-top__mob-logo {
    display: none;
  }

  .mob__social {
    display: none;
  }
}

@media (max-width: 1199px) {
  .features .features__inner::before {
    background-size: cover;
  }

  aside.article-in__sidebar {
    margin-left: 0;
  }
}
