.features {


    .title {
        margin-bottom: 30px;
    }
    
    &__inner {
        position: relative;
        z-index: 1;
        background-color: #DA2F4A;
        padding: 65px 0 75px;
        &::before {
            content: '';
            background: url('../img/02.svg') no-repeat;
            width: 100%;
            height: 100%;
            background-size: contain;
            position: absolute;
            background-size: contain;
            background-position: top;
            z-index: -1;
            top: 0;
        }
        
    }

    &__item {
        color: #fff;
        text-align: center;
        padding: 0 2rem;

        svg {
            width: 80px;
            height: 80px;
            margin-bottom: 30px;
        }

        .text {
            color: #fff;
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        padding-top: 60px;
    }

   
}