.header {
  .navbar-top {
    padding: 1rem 0;
    border-bottom: 6px solid #F2F2F2;

    .nav-item {
      display: flex;
      align-items: center;
      font-size: 15px;
      margin-right: 7rem;

      &:last-child {
        margin-right: 0;
      }

      svg {
        margin-right: 5px;
      }
    }

    .navbar-toggler {
      position: absolute;
    }

    &__container {
      max-width: 1000px;
    }

    &__social {
      a {
        display: flex;
        padding: 5.5px;
        background-color: #F2F2F2;
        border-radius: 10px;
      }
    }
  }

  .navbar-bottom {
    .navbar-nav {
      align-items: center;
      flex: 1;
      justify-content: space-between;
    }

    .nav-item {
      text-transform: uppercase;
      border-bottom: 4px solid #f2f2f2;
      padding-bottom: 5px;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
      &:hover {
        border-bottom: 4px solid #DA2F4A;
      }
    }

    .nav-link {
      padding: 0;
      color: #14052F;
      letter-spacing: 1px;
    }

    &__container {
      max-width: 1045px;
    }

    &__brand {
      max-width: 295px;
      width: 100%;
    }
  }





}


.mob {
  &__social {
    position: absolute;
    right: 12px;

    a {
      padding: 5.5px;
      background-color: #F2F2F2;
      border-radius: 10px;
    }
  }

  &__menu {
    padding-left: 2.7rem;
    margin: 0;

    a {
      text-transform: uppercase;
      text-decoration: none;
      color: #14052F;
      letter-spacing: 1px;
      transition: color 0.15s ease-in-out;
      &:hover {
        color: rgba(0, 0, 0, 0.7);
      }
    }

    li {
      padding-bottom: 1rem;
    }
  }

  &__contact {
    li {
      display: flex;
      align-items: center;
      list-style: none;
    }

    a {
      color: rgba(0, 0, 0, 0.55);
      &:hover {
        color: rgba(0, 0, 0, 0.7);
      }
    }
  }
}


