html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Comfortaa", sans-serif!important;
  overflow-x: hidden;
  min-height: 100vh;
  max-height: 100%;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}


section .title {
  text-transform: uppercase;
  margin: 0;
}

.text {
  font-weight: 400;
  color: #000000;
}

.btn-close {
	position: absolute;
	right: 1.5rem;
	top: 1.5rem;

}