.footer {
  padding: 45px 0;

  &__logo img {
    max-width: 175px;
    width: 100%;
  }

  &__menu {
    padding: 0;
    display: flex;
    margin: 0;
    height: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 0 3rem;

    a {
      text-transform: uppercase;
      text-decoration: none;
      font-size: 13px;
      font-weight: bold;
      color: #191919;
      transition: all 0.15s ease-in-out;

      &:hover {
        color: #ED681B;
      }
    }
  }

  &__copyright {
    display: flex;
    height: 100%;
    align-items: center;

    p {
      color: #A9A9A9;
      font-size: 12px;
      margin: 0;
    }
  }

  &__info {
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin-left: 2rem;
    margin-bottom: 0;

    a {
      text-decoration: none;
      font-size: 12px;
      color: #A9A9A9;
      transition: all 0.15s ease-in-out;

      &:hover {
        color: #ED681B;
      }
    }
  }
}
