.contact {
  &__section {
    background: linear-gradient(90deg, rgba(232,103,37,1) 0%, rgba(218,46,75,1) 30%, rgba(218,46,75,1) 100%);
    z-index: 1;
    position: relative;
  }

  &__form-wrap {
    position: relative;
    height: 100%;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      background: #f4f4f4;
      width: 100vw;
      z-index: -2;
      height: 100%;
    }
  }

  &__form {
    padding: 3rem 4rem;

    &::before {
      content: '';
      background: url('../img/mail.png') no-repeat;
      background-size: contain;
      background-position: bottom right;
      position: absolute;
      top: 15%;
      width: 100%;
      height: 100%;
      z-index: -1;
      
    }

    .title {
      margin-bottom: 2rem;
      color: #5C5C5C;
    }

    .form-control {
      border-radius: 15px;
      padding: 0.475rem 0.75rem 0.475rem 1rem;
      background-color: #F4F4F4;

      &:focus {
        box-shadow: 0 0 0 0.25rem rgba(237, 104, 27, 0.25);
        border-color: #ed681b;
      }
    }

    .form-check-input:checked {
      border-color: #ed681b;
      background-color: #ed681b;
    }

    .form-check-input:focus {
      box-shadow: 0 0 0 0.25rem rgba(237, 104, 27, 0.25);
      border-color: #ed681b;
    }



    .btn-danger {
      font-size: 17px;
      border-radius: 15px;
      max-width: 205px;
      width: 100%;
      padding: 0.4rem 0.75rem;
    }
  }

  &__input-row {
    margin-bottom: 2rem;
  }

  &__input {
    max-width: 465px;
  }

  &__input-mail,
  &__input-phone {
    max-width: 265px;
  }

  &__block {
    padding: 2.5rem 2.5rem 2.5rem 0;
    color: #fff;

    .title {
      margin-bottom: 2rem;
    }

    a {
      color: #fff;
      transition: color 0.15s ease-in-out;

      &:hover {
        color: #14052F;
      }
    }
  }

}

.map {
    height: 425px;
}

.leaflet-control-attribution a {
    display: none;
}

.was-validated .form-check-input:valid:checked {
  border-color: #198754;
}