.btn {
    &__light {
        background-color: #fff;
        border-radius: 20px;
        transition: color 450ms, box-shadow 450ms;
        font-size: 20px;
        font-weight: 400;
        padding: 1rem 0.75rem;
        max-width: 340px;
        width: 100%;
        &:hover {
            box-shadow: -2px 2px 16px rgba(0, 0, 0, 0.6);
            color: #DA2E4B;
        }

        &:focus {
            box-shadow: -2px 2px 16px rgba(0, 0, 0, 0.6);
            color: #DA2E4B;
        }
    }
}


.btn-close:focus {
	box-shadow: 0 0 0 0.25rem rgba(230, 95, 31, 0.22);
}